.header {
    position: fixed;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
    background-color: rgba(0,0,0,.9);
}
.logo{
    width: 80px;
}

.header .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
   
}

.header .nav-menu a {
    color: #ffffff;
    cursor: pointer;
}

/* .header .navbar img {
    width: 250px;
    height: auto;
} */

.header .nav-menu {
    display: flex;
}

.header .nav-item {
    padding: 1rem;
    font-weight: 500;
}
.nav-item .active{
    padding-bottom: 12px;
    border-bottom: 3px solid var(--primary-color);
}

.header .nav-item a:hover {
    color: var(--secondary-color);
}

.hamburger {
    display: none;
}

.logo{
    width: 60px;
    stroke-dasharray: 280;
    stroke-dashoffset: 800;
    animation: animate-logo 4s ease forwards;
}

  @keyframes animate-logo {
    to {
      stroke-dasharray: 800;
      stroke-dashoffset: 5;
  
    }
  }
