@media screen and  (max-width:940px) {
    .header {
        max-width: 100%;
        background-color: rgba(0,0,0,.9);
    }

    .header .navbar {
        max-width: 940px;
        padding: 0 1rem;
    }
  .logo{
      width: 60px;
  }

      

    .hamburger {
        display: block;
    }

    .nav-menu {
        position: fixed;
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgba(0,0,0,.9);
        width: 100%;
        height: 90vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
        padding-top: 90px;
    }

    .header .nav-item {
        padding: 1.5rem;
        font-weight: 500;
      }
      .nav-menu.active {
        left: 0;
    }



   
}
@media screen and  (max-width:940px) and (orientation:landscape){
    .nav-menu {
        position: fixed;
        left: -100%;
        top: 90px;
        flex-direction: row;
        background-color: rgba(0,0,0,.9);
        width: 100%;
        height: 90vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
        padding-top: 0;
        justify-content: center;
       
        
    }
    .header .nav-item {
        align-self: flex-start;
        margin-top: 50px;
       
    }

}

@media screen and (max-width:414px) {
    .header {
        max-width: 414px;
        background-color: rgba(0,0,0,.9);
    }
    .header .nav-menu {
        display: flex;
        width: 100vw;
    }
    
}














