
.room__info__slider .carousel-root {
    width: 50%;
    height: 500px;
    margin: 20px 0;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}
.carousel-slider {
    width: 300px;
    height: 300px;
    object-fit: cover;
}


.carousel .slide img {
    object-fit: cover;
    height: 400px;
}


.carousel img {
    height: 57px;
}

.carousel .thumbs-wrapper {
    margin: 28px;
    display: flex;
    justify-content: center;
    position: relative;
    top: 84px;
    opacity: .5;
}
.carousel.carousel-slider {
    overflow: inherit !important;
}

@media screen and (max-width: 940px) {
    .carousel-root {
        width: 100%;
        margin: 10px 0;
       
    }
  }